@font-face {
  font-family: "Raygun";
  src: url("../assets/fonts/raygun.eot?") format("eot"), url("../assets/fonts/raygun.woff2") format("woff2"), url("../assets/fonts/raygun.woff") format("woff"), url("../assets/fonts/raygun.ttf") format("truetype"), url("../assets/fonts/raygun.svg#Raygun") format("svg");
}

body {
  background-color: white;
}

body,h1,h3 {
  font-family: Raygun, Arial, Helvetica, sans-serif;
}

.container {
  background-color: #a10018;
  background-image: url("../assets/images/bg-red.png");
  background-size: cover;
  background-repeat: repeat-y;
  background-position: top center;
}

.header {
  background-image: url("../assets/images/header-bg.png");
  background-size: contain;
  background-position: top center;
  background-repeat: no-repeat;
  margin: 0 !important;
  box-shadow: 0 -12px 0 #fbc242 inset;
}

@media only screen and (max-width: 40em) {
  .header {
    white-space: normal;
    border-bottom: none;
  }
}

.header-contact {
  margin-top: 70%;
  text-align: center;
}

.contact-title {
  font-size: 1.5em;
  width: 125px;
  margin: 0 auto 5px;
  border-radius: 20px;
  background-color: #fbc242;
  color: #aa0019;
  padding: 2px 0;
}

.contact-subtitle {
  color: white;
  font-size: .8em;
  margin-bottom: 5px;
  text-transform: lowercase;
}

.contact-number {
  color: white;
  font-size: 1.5em;
}

@media only screen and (min-width: 64em) {
  .contact-number {
    font-size: 2em;
  }
}

.contact-addr {
  margin: 0 auto 0;
  line-height: 1.8em;
  color: #aa0019;
  text-transform: uppercase;
}

.contact-addr .contact-delivery-area {
  color: white;
  background-color: rgba(0, 0, 0, 0.2);
  overflow: hidden;
  word-break: break-word;
}

.header-contact a,.pizza-contact a {
  cursor: pointer;
}

.header-contact a:hover,.header-contact a:visited,.pizza-contact a:hover,.pizza-contact a:visited {
  color: white;
  text-decoration: none;
}

.content {
  position: relative;
  border-top: 12px solid white;
  margin: 0 !important;
}

.content-info {
  text-align: center;
  padding: 20px;
  line-height: 1em;
}

.content-info a,.content-info a:visited,.content-info h1,.content-info h3 {
  color: #fff;
  margin: 0 0 10px 0;
  text-transform: uppercase;
}

.content-info a {
  font-size: 20px;
  text-decoration: underline;
}

.content-menu {
  padding: 0 0 66px;
}

@media only screen and (max-width: 40em) {
  .content-menu {
    padding: 0;
  }
}

@media only screen and (min-width: 64em) {
  .content-menu {
    padding-bottom: 110px;
  }
}

.content-side {
  padding: 0;
  text-align: center;
}

.content-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 0;
}

.side-delivery {
  height: 400px;
  width: 200px;
  margin: 50px 0;
  background-image: url("../assets/images/banner-delivery.png");
  background-position: top left;
}

.side-yellow {
  background-image: url("../assets/images/bg-yellow.png");
  padding-bottom: 220px;
}

@media only screen and (max-width: 40em) {
  .side-yellow {
    padding-bottom: 55px;
  }
}

@media only screen and (min-width: 64em) {
  .side-yellow {
    padding-bottom: 110px;
  }
}

.pizza-contact {
  padding: 15px 15px 0;
}

.pizza-contact .contact-title {
  background-color: #aa0019;
  color: white;
}

.pizza-contact .contact-container {
  display: inline-block;
  vertical-align: top;
  margin: 0 10px;
}

.pizza-contact .contact-number,.pizza-contact a,.pizza-contact a:hover,.pizza-contact a:visited {
  color: #aa0019;
}

.pizza-fb {
  padding: 15px;
}

.pizza-fb iframe {
  width: 100%;
  height: 182px;
  border: none;
  overflow: hidden;
  background-color: white;
}

.pizza-menu-wrapper {
  padding: 5px;
  background-color: rgba(0, 0, 0, 0.2);
}

.pizza-menu {
  background-color: transparent;
  table-layout: fixed;
  border-collapse: collapse;
  width: 100%;
  margin: 0;
  border: 2px solid #fbc242;
}

.pizza-menu tr {
  background-color: transparent !important;
  border-bottom: 1px dotted rgba(251, 194, 66, 0.25);
}

.pizza-menu tr:last-child td {
  border-bottom: 2px solid #fbc242;
}

.pizza-menu td,.pizza-menu th {
  width: 20%;
  padding: 5px 0;
  border-left: 2px solid #fbc242;
  border-right: 2px solid #fbc242;
  overflow: hidden;
  color: white;
  text-align: center;
  text-transform: uppercase;
  text-overflow: ellipsis;
  font-size: 1em;
}

@media only screen and (max-width: 40em) {
  .pizza-menu td,  .pizza-menu th {
    width: 16%;
  }
}

.pizza-menu td:first-child,.pizza-menu th:first-child {
  width: auto;
  padding-left: 10px;
  padding-right: 5px;
  text-align: left;
}

.pizza-menu td:last-child,.pizza-menu th:last-child {
  border-right: none;
}

.menu-section {
  border-top: 2px solid #fbc242;
  border-bottom: none !important;
}

.menu-section th {
  position: relative;
  height: 52px;
  font-size: 1.2em;
}

.menu-section th:first-child {
  font-size: 1.5em;
}

.menu-section th > div,.menu-section th::before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 5px;
  padding: 5px;
  line-height: 32px;
  color: #aa0019;
  background-color: #fbc242;
}

.menu-price::before {
  content: "R$ ";
}

@media only screen and (max-width: 40em) {
  .menu-pizza .menu-price::before,  .menu-extra .menu-price::before {
    content: "";
  }
}

.menu-pizza-small::before {
  content: "M";
}

@media only screen and (min-width: 64em) {
  .menu-pizza-small::before {
    content: "Media";
  }
}

.menu-pizza-medium::before {
  content: "G";
}

@media only screen and (min-width: 64em) {
  .menu-pizza-medium::before {
    content: "Grande";
  }
}

.menu-pizza-big::before {
  content: "GG";
}

@media only screen and (min-width: 64em) {
  .menu-pizza-big::before {
    content: "Gigante";
  }
}